<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <div class="btns">
      <div class="btn-main-deepen" @click="$router.push(`/research-orsay/teachingTeam/publicity?teachGroupId=`+table.query.teachGroupId+``)">发布公告</div>
    </div>
    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="35%">标题</th>
        <th width="15%">发布时间</th>
        <th width="15%">阅读人数</th>
        <th width="20%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.title}}</td>
        <td>{{item.createdTime | substrTime }}</td>
        <td style="position: relative">
          <span>{{item.readNum}}</span>
          <!--<i class="el-icon-view oper view" @click="onDialogClick(item.title,item.id)"></i>-->
        </td>
        <td>
          <span @click="$router.push(`/research-orsay/teachingTeam/publicity?id=`+item.id+`&teachGroupId=`+table.query.teachGroupId+``)" class="oper">编辑</span>
          <span @click="remove(item)" class="oper">删除</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>

    <!--阅读详情-->
    <el-dialog :title="title"
               :visible.sync="dialogVisible"
               width="30%">
      <el-tabs @tab-click="onSwitchClick" class="dialog_table">
        <el-tab-pane :label="dialogTable.total+'人已读'"></el-tab-pane>
        <el-tab-pane :label="noDialogTable.total+'人未读'"></el-tab-pane>
        <!--已读-->
        <resize-table>
        <table class="table" v-show="!dialogSwitch">
          <thead>
          <tr>
            <th width="33%">姓名</th>
            <th width="33%">职务</th>
            <th width="33%">阅读时间</th>
          </tr></thead>
          <tbody>
          <tr v-for="item in dialogTable.list">
            <td>{{item.userName}}</td>
            <td>{{item.position}}</td>
            <td>{{item.createdTime | substrTime}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <paging :data.sync="dialogTable" v-show="!dialogSwitch"></paging>
        <resize-table>
        <!--未读-->
        <table class="table" v-show="dialogSwitch">
          <thead>
          <tr>
            <th width="50%">姓名</th>
            <th width="50%">职务</th>
          </tr></thead>
          <tbody>
          <tr v-for="item in noDialogTable.list">
            <td>{{item.userName}}</td>
            <td>{{item.position}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <paging :data.sync="noDialogTable" v-show="dialogSwitch"></paging>
      </el-tabs>
    </el-dialog>
  </div>

</template>
<script>
  export default {
    data () {
      return {
        searchData: [
          {type: 'select', title: '教研组', model: 'teachGroupId', option: []},
          {type: 'input', placeholder: '标题'}],
        table: {
          url: '/notice/queryTeachGroupPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            teachGroupId: null,
            keywords: null
          }
        },
        // 已读
        dialogTable: {
          url: '/notice/queryNoticeReadRecordPage.do',
          el: '.dialog_table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        // 未读
        noDialogTable: {
          url: '/notice/queryNoticeNoReadRecordPage.do',
          el: '.dialog_table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        dialogVisible: false,
        title: null,
        dialogSwitch: false
      }
    },
    methods: {
      // 获取教研组
      async onGetTeachGroupList () {
        let arr = await this.$ajax({
          url: '/teachGroup/queryTeachGroupList.do'
        })
        this.searchData[0].option = []
        arr.forEach(e => {
          this.searchData[0].option.push({
            value: e.id,
            label: e.name
          })
        })
        this.table.query.teachGroupId = this.searchData[0].option[0].value
      },
      // 切换未读已读
      onSwitchClick () {
        this.dialogSwitch = !this.dialogSwitch
      },
      // 点击出现阅读
      onDialogClick (title, id) {
        this.title = title
        this.dialogTable.query.id = id
        this.noDialogTable.query.id = id
        this.$search(this.dialogTable)
        this.$search(this.noDialogTable)
        this.dialogVisible = true
      },
      // 删除
      async remove (item) {
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteNotice.do',
          query: {
            id: item.id,
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      }
    },
    async mounted () {
       await this.onGetTeachGroupList()
       this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .btns
    position: absolute
    right: 15px
    top: 6px
    z-index: 9
  .view
    position: absolute
    right: 8px
    font-size: $font-large
    cursor: pointer
    color: $fc-def !important
    padding: 0 !important

  /deep/ .el-dialog
    width: 50% !important
    .el-dialog__title
      padding: 10px 0
      font-size: $font-large+4
</style>
